<template>
    <div class="titlePopWindow">
      <p>elementUIel-popover
        title title
        title</p>
      <div class="example-line">
        <p title="title">title</p>
        <el-popover
          placement="top-start"
          width="200"
          trigger="hover"
          content="el-popovertitle">
          <p slot="reference">el-popovertitle</p>
        </el-popover>
        <p v-show-title="'title'">title</p>
      </div>
      <p>
        <strong></strong>{{code}} v-show-stitle string  vnode
      </p>
      <p>
        <strong></strong>bindelmouseenter  mouseleave
        mouseentermouseleave
      </p>
      <p>vueextendvue <strong>v-show-stitle</strong> vuerender
       $mount  $el dom body</p>
      <p>vue letvue$destroy,domvuenull</p>
      <ol>
        <strong></strong>
        <li :key="index" v-for="(item, index) in note">{{item}}</li>
      </ol>
      <div>
        <strong></strong>
        <div :key="index" v-for="(item, index) in img">
          <p>{{item.remark}}</p>
          <img :src="item.src"/>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'titlePopWindow',
  data () {
    return {
      code: '<p v-show-title="string/vnode">title</p>',
      img: [
        {remark: 'bind'},
        {remark: 'transitonshowshowprops'},
        {remark: 'vuerenderdombodydom'},
        {remark: 'vueshowfalsenextTick'}
      ],
      note: [
        'clientY ',
        'pageY ()',
        'screenY ',
        'layerY ',
        'offsetY '
      ]
    }
  },
  created () {
    this.img.forEach((a, i) => {
      a.src = require(`../../../static/image/tp${i + 1}.png`)
    })
  }
}
</script>

<style scoped lang="scss">
.example-line{
  display: flex;
  p{
    padding: .2rem .5rem; background: $blue1; color: white;
    margin: 0 .5rem;border-radius: .3rem;
  }
}
</style>



